// Utility functions for storing and retrieving video analysis data
// For MVP, we'll use localStorage

export interface VideoAnalysis {
  id: string;
  filename: string;
  timestamp: string;
  severity: 'High' | 'Medium' | 'Low' | 'None';
  description: string;
  recommendations: string;
  confidence: number;
  fileSize: number;
  videoType: string;
  videoUrl?: string; // For storing the video file
}

const STORAGE_KEY = 'hawkwatch_analyses';

export function saveAnalysis(analysis: VideoAnalysis): void {
  if (typeof window === 'undefined') return;
  
  const existing = getAllAnalyses();
  existing.push(analysis);
  localStorage.setItem(STORAGE_KEY, JSON.stringify(existing));
}

export function getAllAnalyses(): VideoAnalysis[] {
  if (typeof window === 'undefined') return [];
  
  const stored = localStorage.getItem(STORAGE_KEY);
  return stored ? JSON.parse(stored) : [];
}

export function getAnalysisById(id: string): VideoAnalysis | null {
  const analyses = getAllAnalyses();
  return analyses.find(a => a.id === id) || null;
}

export function deleteAnalysis(id: string): void {
  if (typeof window === 'undefined') return;
  
  const analyses = getAllAnalyses().filter(a => a.id !== id);
  localStorage.setItem(STORAGE_KEY, JSON.stringify(analyses));
}

export function generateId(): string {
  return `${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
}

